.input-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 9vh;
  top: 52vh;
}

.search-row {
  border: 2px solid #BABABA;
  box-sizing: border-box;
  border-radius: 8px;
  display: flex;
  width: 82%;
}

.search-icon {
  padding: 1%;
  height: .5vh;
}

.input {
  /* font-size: large; */
  flex-direction: row;
  align-items: center;
  background: transparent;
  border: none;
  width: 100%;
  padding: 1%;
}

.input:focus{
  outline: none;
}